@use "../../foundation/variable" as v;
@use "../../foundation/mixin" as m;
@use "sass:math";

.p-offset-text {
	position: relative;
	bottom: 15%;
	left: 50%;
	transform: translateX(-50%);
	display: flex;
	justify-content: center;

	.words {
		position: relative;
		width: 600px;
		height: 70px;
		font-size: v.$font-size--p-offset-text;

		.word {
			position: absolute;
			width: 100%;
			height: 100%;
			letter-spacing: 10px;
			margin-top: 50px;

			@include m.breakpoint(moon-mini) {
				letter-spacing: inherit;
				margin-top: none;
			}

			.char {
				position: absolute;
				animation: waving-sp 10s linear infinite;

				&:last-child {
					visibility: hidden;
					@include m.breakpoint(moon-mini) {
						visibility: visible;
					}
				}

				@for $i from 1 through 9 {
					&:nth-of-type(#{$i}) {
						animation-delay: math.div($i, -2) +s;
					}
				}

				@include m.breakpoint(moon-mini) {
					offset-path: path(
						"M-64.5,28c44.4,0,44.4,26,88.9,26c44.4,0,44.4-26,88.9-26c44.4,0,44.4,26,88.9,26c44.4,0,44.4-26,88.9-26c44.4,0,44.4,26,88.9,26c44.4,0,44.4-26,88.9-26c44.4,0,44.4,26,88.9,26c44.4,0,44.4-26,88.9-26"
					);
		
					offset-distance: 0;
					animation: waving 10s linear infinite;
				}
			}
		}
	}
}



// offset
@keyframes waving-sp {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(2000%);
	}
}


@keyframes waving {
	100% {
		offset-distance: 100%;
	}
}
